.form-modal {
  position: absolute;
  z-index: 999; }

.modal {
  display: block;
  width: 220px;
  height: auto;
  border: 1px solid #a7a6a6;
  border-radius: 10px;
  background-color: #fff;
  padding: 22px 20px;
  color: #d6d6d6;
  position: relative; }
  .modal__buttons {
    display: flex;
    justify-content: space-between; }
  .modal__toggle {
    color: #000; }
    .modal__toggle button {
      margin-left: 10px;
      margin-bottom: 10px; }
  .modal__close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer; }
    .modal__close:hover {
      fill: #595858; }
  .modal::before {
    content: '';
    display: block;
    width: 0;
    height: 0;
    background-color: transparent;
    border-top: 10px solid transparent;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #43425d;
    position: absolute;
    top: -21px;
    left: 50%;
    z-index: 999; }

.MuiFormControl-root {
  margin-bottom: 20px !important; }

.MuiFormControl-root,
.MuiTextField-root {
  width: 100%; }

.fc-head-container,
.fc-widget-header {
  background-color: #f5f6fa;
  color: #a3a6b4;
  font-weight: 700; }

.fc-unthemed th,
.fc-unthemed td,
.fc-unthemed thead,
.fc-unthemed tbody,
.fc-unthemed .fc-divider,
.fc-unthemed .fc-row,
.fc-unthemed .fc-content,
.fc-unthemed .fc-popover,
.fc-unthemed .fc-list-view,
.fc-unthemed .fc-list-heading td {
  border-color: #eaf0f4; }

.fc-axis,
.fc-widget-header {
  border: none; }

.fc-day-header,
.fc-widget-header,
.fc-fri,
.fc-today {
  border: none; }

.fc-day-header,
.fc-widget-header {
  padding: 10px 0px; }

.fc-center h2 {
  font-size: 18px;
  font-weight: 400;
  color: #4d4f5c; }

.fc-button-primary {
  font-size: 13px;
  background: transparent;
  padding: 8px 18px;
  border: 1px solid #d7dae2;
  color: #4d4f5c;
  text-transform: capitalize; }
  .fc-button-primary:disabled {
    color: #3b86ff;
    background-color: transparent;
    border: 1px solid #d7dae2; }

.fc-button-primary:hover {
  background-color: #d7dae2;
  border-color: #c8cbd2; }

.fc-button-primary:not(:disabled):active,
.fc-button-primary:not(:disabled).fc-button-active {
  color: #3b86ff;
  background-color: transparent;
  border: 1px solid #d7dae2; }

.fc-day,
.fc-widget-content,
.fc-sun,
.fc-other-month,
.fc-past {
  cursor: pointer; }

.fc-event-container .fc-event {
  transition: all 0.2s linear; }

.fc-event-container .fc-event:hover {
  background-color: #6daceb;
  border-color: #6daceb; }

.fc-now-indicator {
  border-color: #3b86ff; }

.fc-unthemed td.fc-today {
  background: #e8ebf5; }

body {
  padding: 0;
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  line-height: 1.2;
}

li {
  font-size: 18px;
  line-height: 1.2;
}

.phone-section {
  padding: 20px 0;
}

.container {
  max-width: 960px;
  margin: 0 auto;
}

p {
  margin: 0;
}

